/* eslint-disable jsx-a11y/anchor-is-valid */
import { React, useEffect, useState } from "react";
import ExperienceCardComponent from "./experienceCardComponent/experienceCardComponent";
import "./experienceComponent.css";
import linea_de_tiempo from "./linea_de_tiempo.svg";

function ExperienceComponent() {
  const url =
    "https://portfolio-back-heroku.herokuapp.com/api/experience/getExperience";
  const [filterCategory, setFilterCategory] = useState([
    "Backend",
    "Frontend",
    "Fullstack",
  ]);
  const [filterYear, setFilterYear] = useState(["2020", "2021"]);
  const [filterCategoryCap, setFilterCategoryCap] = useState("All");
  const [filterYearCap, setFilterYearCap] = useState("All");
  const [experience, setExperience] = useState([]);
  const getExperience = async () => {
    const response = await fetch(url);
    const experienceData = await response.json();
    setExperience(experienceData);
  };
  function addFilterCategory(newFilter) {
    setFilterCategoryCap(newFilter);
    const newFilterArray = filterCategory;
    newFilterArray.push(filterCategoryCap);
    const filtered = newFilterArray.findIndex((filter) => filter === newFilter);
    newFilterArray.splice(filtered, 1);
    setFilterCategory(newFilterArray.sort());
  }
  function addFilterYear(newFilter) {
    setFilterYearCap(newFilter);
    const newFilterArray = filterYear;
    newFilterArray.push(filterYearCap);
    const filtered = newFilterArray.findIndex((filter) => filter === newFilter);
    newFilterArray.splice(filtered, 1);
    setFilterYear(newFilterArray.sort());
  }
  useEffect(() => {
    getExperience();
  }, []);
  return (
    <section className="experience-container">
      <div className="experience-container--text">
        <h3 className="BebasNeue">EXPERIENCIA</h3>
      </div>
      <div className="experience-container--row">
        <div className="dropdown card-content-top">
          <div>
            <a
              className="btn btn-secondary dropdown-toggle"
              href="#"
              role="button"
              id="dropdownMenuLink"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              {filterCategoryCap}
            </a>
            <ul className="dropdown-menu" aria-labelledby="dropdownMenuLink">
              {filterCategory.map((filter) => {
                return (
                  <li key={filter}>
                    <a
                      className="dropdown-item"
                      onClick={() => addFilterCategory(filter)}
                    >
                      {filter}
                    </a>
                  </li>
                );
              })}
            </ul>
          </div>
          <div>
            <a
              className="btn btn-secondary dropdown-toggle experience-container--filter-second--margin"
              href="#"
              role="button"
              id="dropdownMenuLink"
              data-bs-toggle="dropdown"
              aria-expanded="false"
            >
              {filterYearCap}
            </a>
            <ul className="dropdown-menu" aria-labelledby="dropdownMenuLink">
              {filterYear.map((filter) => {
                return (
                  <li key={filter}>
                    <a
                      className="dropdown-item"
                      onClick={() => addFilterYear(filter)}
                    >
                      {filter}
                    </a>
                  </li>
                );
              })}
            </ul>
          </div>
        </div>
        <div className="experience-container--cards card-content-top">
          {experience.map((item) => {
            return (
              <ExperienceCardComponent
                key={item.name}
                data={item}
                filterCategory={filterCategoryCap}
                filterYear={filterYearCap}
              />
            );
          })}
        </div>
        <div className="experience-container--timelineContainer">
          <p className="BebasNeue text-font justify-center">2020</p>
          <img src={linea_de_tiempo}
          className="linea-de-tiempo" alt="linea-de-tiempo"/>
        </div>
      </div>
    </section>
  );
}

export default ExperienceComponent;
