import { React } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faLinkedin,
  faGithub,
  faTwitter,
  faInstagram,
} from "@fortawesome/free-brands-svg-icons";
import "./experienceCardComponent.css";
import Img from "react-cool-img";
const placeHolder = "../../../placeholder/default-placeholder.png";

function ExperienceCardComponent(props) {
  const isVisibleCategory =
    props.filterCategory.toLowerCase() === props.data.category.toLowerCase() ||
    props.filterCategory.toLowerCase() === "all";
  const isVisibleYear =
    props.filterYear.toLowerCase() === props.data.date.inicio.toLowerCase() ||
    props.filterYear.toLowerCase() === "all";
  if (isVisibleCategory && isVisibleYear) {
    return (
      <div className="card-body card mb-3">
        <div className="card-body-content">
          <span className="card-span BebasNeue">
            {props.data.date.inicio} - {props.data.date.final}
          </span>
          <div className="card-img-container">
            <Img
              placeholder={placeHolder}
              src={props.data.img}
              className="card-img lateral-padding img-fluid rounded-start"
              alt={props.data.name + " logo"}
            />
          </div>
        </div>
        <div className="card-margin">
          <div className="d-flex justify-content-between text-align-flex-end">
            <h5 className="card-title BebasNeue">{props.data.name}</h5>
            {props.data.socialNetworks.map((socialNetwork) => {
              switch (socialNetwork.name) {
                case "linkedin":
                  return (
                    <a
                      key={socialNetwork.name}
                      href={socialNetwork.url}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <FontAwesomeIcon
                        icon={faLinkedin}
                        className="social-network-icon-cards lateral-padding"
                      />
                    </a>
                  );
                case "github":
                  return (
                    <a
                      key={socialNetwork.name}
                      href={socialNetwork.url}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <FontAwesomeIcon
                        icon={faGithub}
                        className="social-network-icon-cards lateral-padding"
                      />
                    </a>
                  );
                case "twitter":
                  return (
                    <a
                      key={socialNetwork.name}
                      href={socialNetwork.url}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <FontAwesomeIcon
                        icon={faTwitter}
                        className="social-network-icon-cards lateral-padding"
                      />
                    </a>
                  );
                case "instagram":
                  return (
                    <a
                      key={socialNetwork.name}
                      href={socialNetwork.url}
                      target="_blank"
                      rel="noopener noreferrer"
                    >
                      <FontAwesomeIcon
                        icon={faInstagram}
                        className="social-network-icon-cards lateral-padding"
                      />
                    </a>
                  );
                default:
                  return null;
              }
            })}
          </div>
          <p className="card-text lateral-padding text-break NotoSans">
            {props.data.resume}
          </p>
        </div>
      </div>
    );
  } else {
    return null;
  }
}

export default ExperienceCardComponent;
